.bulma-notification {
  position: fixed;
  z-index: 1000;
  max-width: 350px;
}
.bulma-notification.topRight {
  top: 1.5em;
  right: 1.5em;
}
.bulma-notification.topLeft {
  top: 1.5em;
  left: 1.5em;
}
.bulma-notification.bottomRight {
  bottom: 1.5em;
  right: 1.5em;
}
.bulma-notification.bottomLeft {
  bottom: 1.5em;
  left: 1.5em;
}
.bulma-notification-notice {
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  border: 0 solid rgba(0, 0, 0, 0);
  background: #fff;
  display: block;
  width: auto;
  line-height: 1.5;
  position: relative;
  margin-bottom: 0.5em;
}
.bulma-notification-notice .delete {
  font-size: 1rem;
}
.bulma-notification-fade-enter {
  opacity: 0;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
          animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
.bulma-notification-fade-leave {
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
          animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
.bulma-notification-fade-enter.bulma-notification-fade-enter-active {
  -webkit-animation-name: bulmaNotificationFadeIn;
          animation-name: bulmaNotificationFadeIn;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}
.bulma-notification-fade-leave.bulma-notification-fade-leave-active {
  -webkit-animation-name: bulmaNotificationFadeOut;
          animation-name: bulmaNotificationFadeOut;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}
@-webkit-keyframes bulmaNotificationFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes bulmaNotificationFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes bulmaNotificationFadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes bulmaNotificationFadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}



